export default {
	data() {
		return {
			tab: ['按订单开票','银行卡支付购买开票'],
			activeIndex: 0,
			isActive: true,
			list: [],
			orderList: [
				{
					money: 6,
					orderID: 32275744044503,
					orderTime: '2018-12-20 16:17'
				}
			],
			walletList: [],
		}
		
	},
	onLoad() {
		this.list = this.orderList;
	},
	onNavigationBarButtonTap(e) {
		uni.navigateTo({
			url: 'explain'
		})
	},
	methods: {
		changeTab(index) {
			this.activeIndex = index;
			if(index == 0){
				this.list = this.orderList;
			}else if(index == 1){
				this.list = this.walletList;
			}
		},
		go(url) {
			uni.navigateTo({
				url: url
			})
		},
	}
}
